import _classCallCheck from 'babel-runtime/helpers/classCallCheck';
import _createClass from 'babel-runtime/helpers/createClass';
import { toast } from '../../_util/notification';

var UpImageClass = function () {
    function UpImageClass() {
        _classCallCheck(this, UpImageClass);

        this.upImageEngine = null;
    }

    _createClass(UpImageClass, [{
        key: 'setUpImageEngine',
        value: function setUpImageEngine(upImageEngine) {
            this.upImageEngine = upImageEngine;
        }
    }, {
        key: 'imagePicker',
        value: function imagePicker() {
            // toast('cordova 容器下没有实现此接口--imagePicker');
            // throw Error('upImageEngine is not defined!!!');
            var self = this;
            return new Promise(function (resolve, reject) {
                if (self.upImageEngine) {
                    self.upImageEngine.imagePicker(function (e) {
                        // console.log('getPictureRealPath:0',e);
                        if (e.startsWith('content')) {
                            self.upImageEngine.getPictureRealPath(function (ret) {
                                // console.log('getPictureRealPath:1',ret);
                                if (ret.retCode === '000000') {
                                    resolve({
                                        retCode: '000000',
                                        retInfo: '操作成功',
                                        retData: {
                                            eventType: '',
                                            originalPath: '',
                                            assetPath: ret.retData,
                                            list: []
                                        }
                                    });
                                } else {
                                    reject({
                                        retCode: '900000',
                                        retInfo: 'imagePicker Failed!!!' + ret.retInfo,
                                        retData: ''
                                    });
                                }
                            }, function (ret) {
                                reject(ret);
                            }, e);
                        } else {
                            // console.log('getPictureRealPath:2',e);
                            resolve({
                                retCode: '000000',
                                retInfo: '操作成功',
                                retData: {
                                    eventType: '',
                                    originalPath: '',
                                    assetPath: e,
                                    list: []
                                }
                            });
                        }
                    }, function (e) {
                        // console.log('getPictureRealPath:3',e);
                        reject({
                            retCode: '900000',
                            retInfo: 'imagePicker Failed!!!' + e,
                            retData: ''
                        });
                    }, {
                        quality: 50,
                        sourceType: 2
                    });
                } else {
                    reject({
                        retCode: '',
                        retInfo: 'upImageEngine is not defined!!!',
                        retData: ''
                    });
                }
            });
        }
    }, {
        key: 'closePicker',
        value: function closePicker() {
            toast('cordova 容器下没有实现此接口--closePicker');
            throw Error('upImageEngine is not defined!!!');
        }
    }, {
        key: 'transPath',
        value: function transPath() {
            toast('cordova 容器下没有实现此接口--transPath');
            throw Error('upImageEngine is not defined!!!');
        }
    }, {
        key: 'openCamera',
        value: function openCamera() {
            var self = this;
            return new Promise(function (resolve, reject) {
                if (self.upImageEngine) {
                    self.upImageEngine.imagePicker(function (e) {
                        resolve({
                            retCode: '000000',
                            retInfo: '操作成功',
                            retData: {
                                data: e,
                                base64Data: '',
                                duration: 0
                            }
                        });
                    }, function (e) {
                        reject({
                            retCode: '900000',
                            retInfo: 'imagePicker Failed!!!' + e,
                            retData: ''
                        });
                    }, {
                        quality: 50,
                        sourceType: 1
                    });
                } else {
                    reject({
                        retCode: '',
                        retInfo: 'upImageEngine is not defined!!!',
                        retData: ''
                    });
                }
            });
        }
    }]);

    return UpImageClass;
}();

export default UpImageClass;